import React, { Component } from "react";
import Form from "./Form";
import Tasks from "./Tasks";

import "./Main.css";

export default class Main extends Component {
  /* estado do componente */
  state = {
    newTask: "", // inicializa o state
    tasks: [],
    index: -1,
  };

  /* salvar tarefas no localStorage */ //component... -> funções do react

  //configurar estado de tarefas
  componentDidMount() {
    const tasks = JSON.parse(localStorage.getItem("tasks"));

    if (!tasks) return;

    this.setState({ tasks });
  }

  //prevState -> estado anterior
  componentDidUpdate(prevProps, prevState) {
    const { tasks } = this.state;
    if (tasks === prevState.tasks) return;

    localStorage.setItem("tasks", JSON.stringify(tasks));
  }

  /* método submit para não enviar o form e add task em tasks */
  handleSubmit = (e) => {
    e.preventDefault();

    const { tasks, index } = this.state;
    let { newTask } = this.state;
    newTask = newTask.trim(); //trim elemina os espaços no input

    if (tasks.indexOf(newTask) !== -1) return; //se n existir task

    const newTasks = [...tasks];

    if (index === -1) {
      this.setState({
        tasks: [...newTasks, newTask],
        newTask: "", //p limpar o input
      });
    } else {
      newTasks[index] = newTask;

      this.setState({
        tasks: [...newTasks],
        index: -1,
        newTask: "", //p limpar o input
      });
    }
  };

  /* método que atualiza o estado */
  handleChange = (e) => {
    this.setState({ newTask: e.target.value });
  };

  /* método para editar task */
  handleEdit = (e, index) => {
    console.log("edit", index); //teste

    const { tasks } = this.state;

    this.setState({
      index,
      newTask: tasks[index],
    });
  };

  /* método para deletar task */
  handleDelete = (e, index) => {
    const { tasks } = this.state;
    const newTasks = [...tasks];
    newTasks.splice(index, 1); //pega o índice do array que estou clicando e deleta um dele

    this.setState({
      tasks: [...newTasks],
    });
  };

  render() {
    const { newTask, tasks } = this.state; // pega newTask de this.state
    return (
      <div className="main">
        <div className="content">
          <h1>Lista de tarefas</h1>
          <Form
            handleSubmit={this.handleSubmit}
            handleChange={this.handleChange}
            newTask={newTask}
          />
          <Tasks
            tasks={tasks}
            handleEdit={this.handleEdit}
            handleDelete={this.handleDelete}
          />
        </div>
      </div>
    );
  }
} // isso é um componente que tem estado(é uma classe tbm)
